fieldset {
    margin: 0 0 1em 0;
    border-color: $light-gray;
}

input[type="text"],
input[type="email"],
input[type="url"],
input[type="password"],
input[type="search"],
input[type="number"],
input[type="tel"],
input[type="range"],
input[type="date"],
input[type="month"],
input[type="week"],
input[type="time"],
input[type="datetime"],
input[type="datetime-local"],
input[type="color"],
textarea {
	color: $color__text-input;
	border: 1px solid $color__border-input;
	border-radius: 3px;
	padding: 3px;

	&:focus {
		color: $color__text-input-focus;
	}
}

label {
    font-weight: 800;
    margin-bottom: 0.5em;
    display: block;    
}

select {
	border: 1px solid $color__border-input;
}

textarea {
	width: 100%;
}